@import url('https://fonts.googleapis.com/css2?family=Montserrat&family=Poppins&display=swap');

:root{
    --bg-clr:#f7f7f7;
    --post-clr:white;
    --txt-clr:black ;
    --theme-clr:rgb(235, 236, 2);
    /* --bg-clr: #0c4c7d;
    --post-clr: #093a60;
    --txt-clr: #ffffe6;
    --theme-clr: rgb(7 43 71); */
}
*{
    font-family: 'Poppins', sans-serif;
    user-select: none;
    color: var(--txt-clr);
    /* border: 2px solid red; */
}
.note p:first{
    color: var(--txt-clr);
}
body{
    background: var(--bg-clr);
    overflow-x: hidden !important;
}

.wrapper ,.note-app {
    margin: 6px auto;
    width: calc(100% - 136px);
    /* word-wrap: break-word; */
}                                       
.work-done{
    /* width: 18%; */
    /* margin: 0 auto; */
    font-size: 11px;
    position: absolute;
    top: -4rem;
    left: 50%;
    z-index: 1000;
    transform: translateX(-50%);
 
}
@keyframes notif {
    0%{
        top: -4rem;
    }
    25%{
        top: 3.5rem;
    }
    50%{
        top: 3rem;
    }
    75%{
        top: 3.5rem;
    }
    100%{
        top: -4rem;
    }
}
.work-done p{
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background: white;
    padding: 9px;
    
    border-radius: 10px;
    box-shadow: 0px 0px 15px rgba(0,0,0,0.09);
}
.note-app{
    height: 100%;
}
.tool-area{
    position: sticky;
    padding-top: 9px;
    top: 0;
    z-index: 100;
    background: var(--bg-clr);
}
.tools{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 5px;
    padding-top: 6px;
}
.tool-icon{
    box-shadow: 0px 0px 8px rgba(0,0,0,0.04);
    border-radius: 4px;
    font-size: 22px;
    color: var(--theme-clr);
    margin-right: 11px;
}
.check-icon{
    font-size: 23px;
}
.search-section{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 14px;
}
#search-input{
    font-size: 11px;
    width: 680px;
    border: none;
    padding: 8px 13px;
    border-radius: 20px;
    box-shadow: 2px 0 8px rgba(0,0,0,0.02);
    outline: none;
    background-color: var(--post-clr);
}
.tag-section{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding-bottom: .7rem;
    gap: 8px;
    font-size: 10px;
}
.tag   {
    background: var(--post-clr);
    padding: 8px 13px;
    border-radius: 20px;
    display: flex;
    transition: all .3s ease;
}
.select , .dlt {
    display: flex;
    padding: 5px 8px;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
   
}

.select, .dlt  , .add_favourite {
    font-size: 20px;
    background: var(--post-clr);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    margin-right: .3rem;
}
.heart{
    
    padding: 5px 8px;
}
.select-con{
    width: 56%;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    padding-bottom:8px ;
   
}
.dlt , .add_favourite{
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease;
    cursor: pointer;
    user-select: none;
}
.dlt:hover ,.select:hover, .add_favourite:hover{
    background: rgba(0, 0, 0, 0.05);
}
.add_favourite:first-child.show_fav{
    padding: 6px 8px;
}
.add_favourite{
    font-size: 11px;
    cursor: pointer;
    user-select: none;
}
.show_dlt , .show_fav{
    opacity: 1;
    visibility: visible;
    transition: all .3s ease;
}
.action-box{
    display: flex;
    margin-left: 6px;
}
.action-box span{
    font-size: 15px;
}
.add-tag{
    display: none;
}
.notes{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    overflow: overlay;
    border-radius: 16px;
    height: 60vh;
    position: absolute;
    width: 54%;
    margin: 1px auto;
    left: 50%;
    transform: translateX(-50%);
}
.note{
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: flex-start;
    width: 90%;
    background: var(--post-clr);
    padding: 10px 18px;
    border-radius: 16px;
    font-size: 10px;
    margin: .5rem 0;
    box-shadow: 2px 0 12px rgba(0,0,0,0.02);
    transition: all .3s ease;
    color: var(--txt-clr);
    /* border-left: 2px solid yellow;
    border-right: 2px solid yellow; */
    /* overflow: hidden; */
    animation:anim .3 ease infinite ; 
}
@keyframes anim{
    0%{
        margin-top: -1rem;
    }100%{
        margin-top: .5rem;
    }
}
.manage_padding{
    padding-right: 30px;
}
.note:hover{
    transform: scale(.95);
}
.add-icon:hover{
    transform: scale(.9);
}
.note-title{
    font-size: 10px;
    /* font-weight: 600; */
    margin: 0;
}
.note .checkBox{
    border-radius: 8px;
    background-color: var(--theme-clr);
}
.checkBox-con{
    display: none;
}
.show_check_box{
    display: block;
}
.checkBox-con input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
    /* border:1px solid red; */
  }
/* Create a custom checkbox */
.checkmark {
    position: absolute;
    top: 24px;
    right: 13px;
    height: 17px;
    width: 17px;
    background-color: #eee;
    /* border:1px solid red; */
    border-radius:6px;
  }
  
  /* On mouse-over, add a grey background color */
  /* .container:hover input ~ .checkmark {
    background-color: #ccc;
  }
   */
  /* When the checkbox is checked, add a blue background */
  .checkBox-con input:checked ~ .checkmark {
    background-color: var(--theme-clr);
  }
  
  /* Create the checkmark/indicator (hidden when not checked) */
  .checkmark:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the checkmark when checked */
  .checkBox-con input:checked ~ .checkmark:after {
    display: block;
  }
  
  /* Style the checkmark/indicator */
  .checkBox-con .checkmark:after {
    left: 6px;
    top: 2px;
    width: 3px;
    height: 8px;
    border: solid white;
    border-width: 0 3px 3px 0;
    border-radius: 1px;
    transform: rotate(45deg);
  }


.note p{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    width: -webkit-fill-available;
    padding-right: 1rem;
}
.note-pg , .note-node{
    margin: 1px 0;
    color: rgba(0,0,0,0.6);
}
.note-node{
    font-size: 8px;
    margin: 0px ;
    color: rgba(0,0,0,0.4);
}
.add-icon{
    position: fixed;
    bottom: 4.5rem;
    right: 21rem;
    display: flex;
    justify-content: center;
    align-items: center;
    background:var(--theme-clr); 
    color: white;
    border-radius: 50%;
    font-size: 1.6rem;
    width: 42px;
    height: 42px;
    box-shadow: 2px 0 8px rgba(0,0,0,0.05);
    cursor: pointer;
    transition: all .3s ease;
}
::-webkit-scrollbar{
    opacity: 0;
}
.nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 60px;
    height: 56px;
}

.right-top-nav {
    display: flex;
}

.right-top-nav i {
    margin-right: .5rem;
}

.input {
    margin: 0 60px;
    outline: none;
    resize: none;
    width: calc(100% - 136px);
    border: none;
    font-size: 18px;
    /* font-family: system-ui; */
    overflow: hidden;
    /* min-height: 29px; */
    background-color: transparent;
}
.title-input{
    height: 29px;
}

.count-detail {
    display: flex;
    align-items: center;
    padding: 2px 0px;
    margin: 0px auto;
    width: calc(100% - 120px);
    justify-content: flex-start;
    font-size: 10px;
    color: rgba(0, 0, 0, 0.5);
    cursor: default;
    /* border-top: 1px solid rgba(0, 0, 0, 0.1); */
}
.count-detail *{

    color: rgba(0, 0, 0, 0.4);
}

.box {
    display: flex;
    align-items: center;
    padding: 0 5px;
}

.box span {
    margin-right: 3px;
}
textarea{
    caret-color: var(--theme-clr);
}

.editor-body textarea {
    font-size: 13px;
    margin: 5px 62px;
    color: rgba(0,0,0,0.8);
    /* min-height: 460px !important; */
    /* border: 2px solid red; */
}
.view{
    font-size: 14px;
    margin: 6px auto;
    width: calc(100% - 120px);
    /* border: 2px solid red; */
    background-color:var(--bg-clr);
    padding: .5rem;
}
.view pre{
    white-space: pre-wrap;
}
.icon{
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    user-select: none;
    color: var(--txt-clr);
    transition: all .2s ease;
    /* border: 1px solid red; */
}
.icon:hover {
    transform: scale(.8);
}
.back-icon:hover{
    transform:rotateY(180deg)  scale(.8) !important;
}
textarea::placeholder{
    color: rgba(0, 0, 0, 0.2);
}
.editor-panel{
    position:fixed;
    width: 100vw;
    min-height: 100vh;
    z-index: 100;
    top: 0;
    left: 100vw;
    box-shadow: 2px 0 8px rgba(0,0,0,0.05);
    background: var(--post-clr);
    transition: all .6s ease;
}
.open-panel{
    position:absolute;
    width: 100vw;
    min-height: 100vh;
    z-index: 100;
    top: 0;
    left: 0;
    box-shadow: 2px 0 8px rgba(0,0,0,0.05);
    background: var(--post-clr);
    transition: all .6s ease;
}
.fade-clr:first{
    color:rgba(0, 0, 0, 0.6);
}
.fade-clr:last-child{
    color:rgba(0, 0, 0, 0.4);
}
 

@media screen and (max-width:769px) {
    .select-con{
        width: 78%;
    }
    .notes{
        width: 65%;
    }
    .wrapper ,.note-app {
        width: calc(100% - 156px);
    }
}

@media screen and (max-width:542px) {
    .select-con{
        width: 90%;
    }
    .notes{
        width: 94%;
        position: static;
        transform: translateX(0px) ;
        height: 68vh;
    }
    .wrapper ,.note-app {
        width: calc(95%);
        /* border: 2px solid red; */
        margin-bottom: 2px;
    }
    .note{
        width: 83%;
    }
    .nav{
        padding: 0 18px;
    }
    .editor-title{
        padding: 0;
    }
    .input{
        width: 90%;
        margin: 0 16px;
        font-size: 15px;
    }
    .editor-body textarea{
        margin: 0 16px;
        font-size: 11px;
        
    }
    .count-detail{
        width: calc( 100% - 27px );
        font-size:9px ;
    }
    .add-icon{
        bottom: 2.5rem;
        right: 2rem;
    }
}